@font-face {
  font-family: 'Dignitas Italic';
  src: url('Dignitas-Italic.eot');
  src: local('.'),
    url('../fonts/Dignitas-Italic.eot?#iefix') format('embedded-opentype'),
    url('../fonts/Dignitas-Italic.woff2') format('woff2'),
    url('../fonts/Dignitas-Italic.woff') format('woff'),
    url('../fonts/Dignitas-Italic.ttf') format('truetype');
}

@font-face {
  font-family: 'Dignitas Regular';
  src: url('Dignitas-Regular.eot');
  src: local('.'),
    url('../fonts/Dignitas-Regular.eot?#iefix') format('embedded-opentype'),
    url('../fonts/Dignitas-Regular.woff2') format('woff2'),
    url('../fonts/Dignitas-Regular.woff') format('woff'),
    url('../fonts/Dignitas-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Dignitas Bold';
