  src: local('.'),
    url('../fonts/Dignitas-Bold.eot?#iefix') format('embedded-opentype'),
    url('../fonts/Dignitas-Bold.woff2') format('woff2'),
    url('../fonts/Dignitas-Bold.woff') format('woff'),
    url('../fonts/Dignitas-Bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Dignitas Bold Italic';
  src: url('Dignitas-Bold-Italic.eot');
