// tradegothic
@font-face {
  font-family: "TradeGothic";
  src: url(data:application/font-woff2;charset=utf-8;base64,) format("woff2"),
    url(data:application/font-woff;charset=utf-8;base64,) format("woff"),
    url("../fonts/tradegothic/TradeGothic.ttf") format("truetype"),
    url("../fonts/tradegothic/TradeGothic.svg#TradeGothic") format("svg");
  font-weight: normal;
  font-style: normal;
}

// tradegothiccond
@font-face {
  font-family: "TradeGothic CondEighteen";
  src: url(data:application/font-woff2;charset=utf-8;base64,) format("woff2"),
    url(data:application/font-woff;charset=utf-8;base64,) format("woff"),
    url("../fonts/tradegothiccond/hinted-TradeGothic-BoldCondTwenty.ttf") format("truetype"),
    url("../fonts/tradegothiccond/hinted-TradeGothic-BoldCondTwenty.svg#TradeGothic-BoldCondTwenty") format("svg");
  font-weight: bold;
  font-style: normal;
}
