.facet-item {
  padding-bottom: 0.3rem;
  font-family: "Dignitas Regular";
  font-size: 1.3rem;
}

@media (max-width: 1200px) {
  .facet-item {
    font-size: calc(1.03rem + 0.36vw) ;
  }
}

input.facets-checkbox {
  margin-right: 1rem;
  line-height: 1;
  border-radius: 9;
}

.facet-item {
  display: flex;
  align-items: center;
}

.facet-item input {
  height: 20px;
  width: 20px;
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  border-radius: 0;
  outline: none;
  transition-duration: 0.3s;
  cursor: pointer;
}

.blazy__caption {
  padding: .5rem 1rem 0 1rem;
}

@media (min-width: 80em) {
  .blazy__caption {
    padding: .5rem 1rem 0 0rem;
  }
}

.blazy__caption,
.blazy__caption p,
.slide__description {
  font-size: 0.9rem;
  color: #000;
  font-family: "Dignitas Regular";
  line-height: 1;
}
